.taroify-uploader {
  position: relative;
  display: inline-block;
}
.taroify-uploader__wrapper {
  display: flex;
  flex-wrap: wrap;
}
.taroify-uploader__wrapper--disabled {
  opacity: var(--uploader-disabled-opacity, var(--disabled-opacity, 0.5));
}
.taroify-uploader__wrapper--disabled .taroify-uploader__upload:active {
  background: var(--uploader-upload-background-color, var(--gray-1, #f7f8fa));
}
.taroify-uploader__upload {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: var(--uploader-upload-width, var(--uploader-upload-size, var(--uploader-size, 160px)));
  height: var(--uploader-upload-height, var(--uploader-upload-size, var(--uploader-size, 160px)));
  margin: var(--uploader-upload-margin, 0 var(--padding-xs, 16px) var(--padding-xs, 16px) 0);
  background: var(--uploader-upload-background-color, var(--gray-1, #f7f8fa));
}
.taroify-uploader__upload:active {
  background: var(--uploader-upload-active-background-color, var(--active-color, var(--gray-2, #f2f3f5)));
}
.taroify-uploader__upload--readonly:active {
  background: var(--uploader-upload-background-color, var(--gray-1, #f7f8fa));
}
.taroify-uploader__upload-icon {
  font-size: var(--uploader-upload-icon-font-size, 48px);
  color: var(--uploader-upload-icon-color, var(--gray-4, #dcdee0));
}
.taroify-uploader__upload-text {
  margin-top: var(--uploader-upload-text-margin-top, var(--padding-xs, 16px));
  font-size: var(--uploader-upload-text-font-size, var(--font-size-sm, 24px));
  color: var(--uploader-upload-text-color, var(--gray-6, #969799));
}
.taroify-uploader__preview {
  position: relative;
  margin: var(--uploader-preview-margin, 0 var(--padding-xs, 16px) var(--padding-xs, 16px) 0);
  cursor: pointer;
}
.taroify-uploader__preview-image {
  display: block;
  width: var(--uploader-preview-image-width, var(--uploader-preview-image-size, var(--uploader-size, 160px)));
  height: var(--uploader-preview-image-height, var(--uploader-preview-image-size, var(--uploader-size, 160px)));
  overflow: hidden;
}
.taroify-uploader__preview-remove {
  position: absolute;
  top: 0;
  right: 0;
  width: var(--uploader-remove-width, var(--uploader-remove-size, 28px));
  height: var(--uploader-remove-height, var(--uploader-remove-size, 28px));
  background: var(--uploader-remove-background-color, rgba(0, 0, 0, 0.7));
  border-radius: var(--uploader-remove-border-radius, 0 0 0 24px);
}
.taroify-uploader__preview-remove-icon {
  position: absolute;
  top: -4px;
  right: -4px;
  font-size: var(--uploader-remove-icon-font-size, 32px);
  color: var(--uploader-remove-icon-color, var(--white, #fff));
  transform: scale(0.5);
}
.taroify-uploader__preview-cover {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.taroify-uploader__mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--uploader-mask-color, var(--white, #fff));
  background: var(--uploader-mask-background-color, rgba(50, 50, 51, 0.88));
}
.taroify-uploader__mask-icon {
  font-size: var(--uploader-mask-icon-size, 44px);
}
.taroify-uploader__mask-message {
  padding: var(--uploader-mask-message-padding, 0 var(--padding-base, 8px));
  margin-top: var(--uploader-mask-message-margin-top, 12px);
  font-size: var(--uploader-mask-message-font-size, var(--font-size-sm, 24px));
  line-height: var(--uploader-mask-message-line-height, var(--line-height-xs, 28px));
}
.taroify-uploader__loading {
  width: var(--uploader-loading-icon-width, var(--uploader-loading-icon-size, 44px));
  height: var(--uploader-loading-icon-height, var(--uploader-loading-icon-size, 44px));
  color: var(--uploader-loading-icon-color, var(--white, #fff));
}
.taroify-uploader__file {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: var(--uploader-file-width, var(--uploader-file-size, var(--uploader-size, 160px)));
  height: var(--uploader-file-height, var(--uploader-file-size, var(--uploader-size, 160px)));
  background: var(--uploader-file-background-color, var(--background-color, var(--gray-1, #f7f8fa)));
}
.taroify-uploader__file-icon {
  font-size: var(--uploader-file-icon-font-size, 40px);
  color: var(--uploader-file-icon-color, var(--gray-7, #646566));
}
.taroify-uploader__file-name {
  box-sizing: border-box;
  width: 100%;
  padding: var(--uploader-file-name-padding, 0 var(--padding-base, 8px));
  margin-top: var(--uploader-file-name-margin-top, var(--padding-xs, 16px));
  font-size: var(--uploader-file-name-font-size, var(--font-size-sm, 24px));
  color: var(--uploader-file-name-color, var(--gray-7, #646566));
  text-align: center;
}