.taroify-cell {
  position: relative;
  box-sizing: border-box;
  display: flex;
  width: 100%;
  padding: var(--cell-vertical-padding, 20px) var(--cell-horizontal-padding, var(--padding-md, 32px));
  overflow: hidden;
  font-size: var(--cell-font-size, var(--font-size-md, 28px));
  line-height: var(--cell-line-height, 48px);
  color: var(--cell-color, var(--text-color, var(--gray-8, #323233)));
  background: var(--cell-background-color, var(--white, #fff));
}
.taroify-cell::after {
  position: absolute;
  box-sizing: border-box;
  pointer-events: none;
  content: " ";
  right: var(--padding-sm, 24px);
  bottom: 0;
  left: var(--padding-sm, 24px);
  border-bottom: 2px solid var(--cell-border-color, var(--border-color, var(--gray-3, #ebedf0)));
  transform: scaleY(0.5);
}
.taroify-cell:last-child::after, .taroify-cell--borderless::after {
  display: none;
}
.taroify-cell__title {
  flex: 1;
}
.taroify-cell__brief {
  margin-top: var(--cell-brief-margin-top, var(--padding-base, 8px));
  font-size: var(--cell-brief-font-size, var(--font-size-sm, 24px));
  line-height: var(--cell-brief-line-height, var(--line-height-sm, 36px));
  color: var(--cell-brief-color, var(--gray-6, #969799));
}
.taroify-cell--start {
  align-items: flex-start;
}
.taroify-cell--center {
  align-items: center;
}
.taroify-cell--end {
  align-items: flex-end;
}
.taroify-cell--large {
  padding-top: var(--cell-vertical-padding-large, var(--padding-sm, 24px));
  padding-bottom: var(--cell-vertical-padding-large, var(--padding-sm, 24px));
}
.taroify-cell--large .taroify-cell__title {
  font-size: var(--cell-title-font-size-large, var(--font-size-lg, 32px));
}
.taroify-cell--large .taroify-cell__subtitle {
  font-size: var(--cell-subtitle-font-size-large, var(--font-size-md, 28px));
}
.taroify-cell--clickable {
  cursor: pointer;
}
.taroify-cell--clickable:active {
  background: var(--cell-active-color, var(--active-color, var(--gray-2, #f2f3f5)));
}
.taroify-cell--required {
  overflow: visible;
}
.taroify-cell--required::before {
  position: absolute;
  left: var(--padding-xs, 16px);
  font-size: var(--cell-font-size, var(--font-size-md, 28px));
  color: var(--cell-required-color, var(--danger-color, var(--red, #ee0a24)));
  content: "*";
}
.taroify-cell__value {
  flex: 1;
  color: var(--cell-value-color, var(--gray-6, #969799));
  text-align: right;
}
.taroify-cell__value--alone {
  color: var(--gray-8, #323233);
  text-align: left;
}
.taroify-cell__icon, .taroify-cell__right-icon {
  height: var(--cell-line-height, 48px);
  font-size: var(--cell-icon-size, 32px);
  line-height: var(--cell-line-height, 48px);
}
.taroify-cell__icon {
  display: flex;
  align-items: center;
  margin-right: var(--cell-icon-margin-left, 8px);
}
.taroify-cell__right-icon {
  display: flex;
  align-items: center;
  margin-left: var(--cell-right-icon-margin-right, 8px);
  color: var(--cell-value-color, var(--gray-6, #969799));
}

.taroify-cell-group {
  background: var(--cell-group-background-color, var(--white, #fff));
}
.taroify-cell-group--inset {
  margin: var(--cell-group-inset-padding, 0 var(--padding-md, 32px));
  overflow: hidden;
  border-radius: var(--cell-group-inset-border-radius, var(--border-radius-lg, 16px));
}
.taroify-cell-group__title {
  padding: var(--cell-group-title-padding, var(--padding-md, 32px) var(--padding-md, 32px) var(--padding-xs, 16px));
  font-size: var(--cell-group-title-font-size, var(--font-size-md, 28px));
  line-height: var(--cell-group-title-line-height, 32px);
  color: var(--cell-group-title-color, var(--gray-6, #969799));
}
.taroify-cell-group__title--inset {
  padding: var(--cell-group-inset-title-padding, var(--padding-md, 32px) var(--padding-md, 32px) var(--padding-xs, 16px) var(--padding-xl, 64px));
}