  border: 0;
}
.taroify-stepper__decrease::before, .taroify-stepper__increase::before {
  width: 50%;
  height: 2px;
}
.taroify-stepper__decrease::after, .taroify-stepper__increase::after {
  width: 2px;
  height: 50%;
}
.taroify-stepper__decrease::before, .taroify-stepper__decrease::after, .taroify-stepper__increase::before, .taroify-stepper__increase::after {
  position: absolute;
  top: 50%;
