}
.taroify-stepper__decrease--disabled:active, .taroify-stepper__increase--disabled:active {
  background: var(--stepper-button-disabled-background-color, var(--background-color, var(--gray-1, #f7f8fa)));
}
.taroify-stepper__decrease::after {
  display: none;
}
.taroify-stepper__input {
  box-sizing: border-box;
  display: inline-block;
  width: var(--stepper-input-width, 64px);
  height: var(--stepper-input-height, 56px);
