  border: 0 none;
  border-radius: 0;
  appearance: none;
}
.taroify-stepper__input:disabled {
  color: var(--stepper-input-disabled-color, var(--gray-5, #c8c9cc));
  background: var(--stepper-input-disabled-background-color, var(--active-color, var(--gray-2, #f2f3f5)));
