}
.taroify-stepper--rounded .taroify-stepper__decrease {
  border-radius: var(--stepper-rounded-decrease-button-border-radius, var(--stepper-rounded-button-border-radius, var(--border-radius-md, 8px))) 0 0 var(--stepper-rounded-decrease-button-border-radius, var(--stepper-rounded-button-border-radius, var(--border-radius-md, 8px)));
}
.taroify-stepper--rounded .taroify-stepper__increase {
  border-radius: 0 var(--stepper-rounded-increase-button-border-radius, var(--stepper-rounded-button-border-radius, var(--border-radius-md, 8px))) var(--stepper-rounded-increase-button-border-radius, var(--stepper-rounded-button-border-radius, var(--border-radius-md, 8px))) 0;
}
.taroify-stepper--circular .taroify-stepper__input {
