.taroify-native-input {
  box-sizing: inherit;
  display: inherit;
  width: inherit;
  min-width: inherit;
  height: inherit;
  line-height: inherit;
  color: inherit;
  resize: inherit;
  background: inherit;
  border: inherit;
}
.taroify-native-input[type=date], .taroify-native-input[type=time], .taroify-native-input[type=datetime-local] {
  min-height: var(--cell-line-height, var(--cell-line-height, 48px));
}
.taroify-native-input[type=search] {
  appearance: none;
}
.taroify-native-input::placeholder {
  color: var(--input-placeholder-color, var(--text-color-3, var(--gray-5, #c8c9cc)));
}
.taroify-native-input--readonly {
  cursor: default;
}
.taroify-native-input--colorful::placeholder {
  color: var(--input-placeholder-color, var(--text-color-3, var(--gray-5, #c8c9cc)));
}

.taroify-input {
  box-sizing: border-box;
  display: block;
  width: 100%;
  min-width: 0;
  height: var(--input-height, 48px);
  padding: 0;
  margin: 0;
  font-size: var(--input-font-size, var(--font-size-md, 28px));
  line-height: var(--input-line-height, 48px);
  color: var(--input-color, var(--text-color, var(--gray-8, #323233)));
  resize: none;
  background-color: transparent;
  border: 0;
  outline: none;
}
.taroify-input[type=date], .taroify-input[type=time], .taroify-input[type=datetime-local] {
  min-height: var(--cell-line-height, var(--cell-line-height, 48px));
}
.taroify-input[type=search] {
  appearance: none;
}
.taroify-input__placeholder {
  color: var(--input-placeholder-color, var(--text-color-3, var(--gray-5, #c8c9cc)));
}
.taroify-input--disabled {
  color: var(--input-disabled-color, var(--text-color-3, var(--gray-5, #c8c9cc)));
  cursor: not-allowed;
  opacity: 1;
  -webkit-text-fill-color: var(--input-disabled-color, var(--text-color-3, var(--gray-5, #c8c9cc)));
}
.taroify-input--readonly, .taroify-input__placeholder--readonly {
  color: var(--input-color, var(--text-color, var(--gray-8, #323233)));
}
.taroify-input--readonly {
  cursor: default;
}
.taroify-input--primary, .taroify-input__placeholder--primary {
  color: var(--input-primary-color, var(--primary-color, var(--blue, #1989fa)));
}
.taroify-input--info, .taroify-input__placeholder--info {
  color: var(--input-info-color, var(--info-color, var(--cyan, #2db7f5)));
}
.taroify-input--success, .taroify-input__placeholder--success {
  color: var(--input-success-color, var(--success-color, var(--green, #07c160)));
}
.taroify-input--warning, .taroify-input__placeholder--warning {
  color: var(--input-warning-color, var(--warning-color, var(--orange, #ff976a)));
}
.taroify-input--danger, .taroify-input__placeholder--danger {
  color: var(--input-danger-color, var(--danger-color, var(--red, #ee0a24)));
}
.taroify-input--left {
  justify-content: flex-start;
  text-align: left;
}
.taroify-input--center {
  justify-content: center;
  text-align: center;
}
.taroify-input--right {
  justify-content: flex-end;
  text-align: right;
}
.taroify-input__clear {
  flex-shrink: 0;
  padding: 0 var(--padding-xs, 16px);
  margin-right: calc(var(--padding-xs, 16px) * -1);
  font-size: var(--input-clear-icon-size, 32px);
  line-height: inherit;
  color: var(--input-clear-icon-color, var(--gray-5, #c8c9cc));
  cursor: pointer;
}