.taroify-picker {
  position: relative;
  overflow: hidden;
  user-select: none;
  background-color: var(--picker-background-color, var(--white, #fff));
  text-size-adjust: 100%;
  /* avoid iOS text size adjust */
}
.taroify-picker__toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: var(--picker-toolbar-height, 88px);
}
.taroify-picker__cancel, .taroify-picker__confirm {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: var(--picker-action-padding, 0 var(--padding-md, 32px));
  font-size: var(--picker-action-font-size, var(--font-size-md, 28px));
  cursor: pointer;
  background-color: transparent;
  border: none 0;
}
.taroify-picker__cancel:active, .taroify-picker__confirm:active {
  opacity: var(--active-opacity, 0.6);
}
.taroify-picker__confirm {
  color: var(--picker-confirm-action-color, var(--text-link-color, var(--blue, #1989fa)));
}
.taroify-picker__cancel {
  color: var(--picker-cancel-action-color, var(--gray-6, #969799));
}
.taroify-picker__title {
  max-width: 50%;
  font-size: var(--picker-title-font-size, var(--font-size-lg, 32px));
  font-weight: var(--font-weight-bold, 500);
  line-height: var(--picker-title-line-height, var(--line-height-md, 40px));
  text-align: center;
}
.taroify-picker__loading {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--picker-loading-icon-color, var(--primary-color, var(--blue, #1989fa)));
  background-color: var(--picker-loading-mask-color, rgba(255, 255, 255, 0.9));
}
.taroify-picker__columns {
  position: relative;
  display: flex;
  cursor: grab;
}
.taroify-picker__mask {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-image: linear-gradient(180deg, hsla(0deg, 0%, 100%, 0.9), hsla(0deg, 0%, 100%, 0.4)), linear-gradient(0deg, hsla(0deg, 0%, 100%, 0.9), hsla(0deg, 0%, 100%, 0.4));
  background-repeat: no-repeat;
  background-position: top, bottom;
  transform: translateZ(0);
}
.taroify-picker__frame {
  position: absolute;
  top: 50%;
  right: var(--padding-md, 32px);
  left: var(--padding-md, 32px);
  z-index: 2;
  pointer-events: none;
  transform: translateY(-50%);
}

.taroify-picker-column {
  flex: 1;
  overflow: hidden;
  font-size: var(--picker-option-font-size, var(--font-size-lg, 32px));
}
.taroify-picker-column__wrapper {
  transition-timing-function: cubic-bezier(0.23, 1, 0.68, 1);
}
.taroify-picker-column__wrapper--zero {
  transition-duration: var(--picker-column-transition-zero-duration, 0);
}
.taroify-picker-column__wrapper--momentum {
  transition-duration: var(--picker-column-transition-duration, var(--picker-swipe-transition-duration, 800ms));
}
.taroify-picker-column__wrapper--switch {
  transition-duration: var(--picker-column-transition-switch-duration, 200ms);
}

.taroify-picker-option {
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--picker-option-height, 44Px);
  padding: var(--picker-option-padding, 0 var(--padding-base, 8px));
  color: var(--picker-option-color, var(--black, #000));
}
.taroify-picker-option--disabled {
  cursor: not-allowed;
  opacity: var(--picker-option-disabled-opacity, 0.3);
}