.taroify-form-label {
  box-sizing: border-box;
  display: flex;
  flex: none;
  width: var(--form-label-width, 6.2em);
  margin-right: var(--form-label-margin-right, var(--padding-sm, 24px));
  color: var(--form-label-color, var(--gray-7, #646566));
  text-align: left;
  word-wrap: break-word;
}
.taroify-form-label--left {
  justify-content: flex-start;
  text-align: left;
}
.taroify-form-label--center {
  justify-content: center;
  text-align: center;
}
.taroify-form-label--right {
  justify-content: flex-end;
  text-align: right;
}
.taroify-form-label--required::before {
  margin-right: 4px;
  color: var(--form-label-required-color, var(--red, #ee0a24));
  content: "*";
}

.taroify-form-item > .taroify-cell__value {
  text-align: initial;
}
.taroify-form-item--disabled .taroify-form__label {
  color: var(--form-label-disabled-color, var(--gray-5, #c8c9cc));
}
.taroify-form-item__value {
  overflow: visible;
}
.taroify-form-item__icon, .taroify-form-item__right-icon {
  flex-shrink: 0;
}
.taroify-form-item__right-icon {
  padding: var(--form-item-right-icon-padding, 0 var(--padding-xs, 16px));
  margin-right: var(--form-item-right-icon-margin-right, -16px);
  line-height: inherit;
}
.taroify-form-item__icon.taroify-icon, .taroify-form-item__right-icon.taroify-icon {
  display: block;
  font-size: var(--form-item-icon-size, 32px);
  line-height: inherit;
}
.taroify-form-item .taroify-input + .taroify-button {
  flex-shrink: 0;
  margin-left: var(--form-item-right-button-margin-left, var(--padding-xs, 16px));
}

.taroify-form-control {
  display: flex;
  align-items: center;
  min-height: var(--form-control-min-height, var(--cell-line-height, 48px));
  color: var(--form-control-color, var(--text-color, var(--gray-8, #323233)));
}
.taroify-form-control--left {
  justify-content: flex-start;
  text-align: left;
}
.taroify-form-control--center {
  justify-content: center;
  text-align: center;
}
.taroify-form-control--right {
  justify-content: flex-end;
  text-align: right;
}

.taroify-form-feedback {
  font-size: var(--form-feedback-font-size, var(--font-size-sm, 24px));
}
.taroify-form-feedback--left {
  text-align: left;
}
.taroify-form-feedback--center {
  text-align: center;
}
.taroify-form-feedback--right {
  text-align: right;
}
.taroify-form-feedback--valid {
  color: var(--form-feedback-valid-color, var(--success-color, var(--green, #07c160)));
}
.taroify-form-feedback--warning {
  color: var(--form-feedback-warning-color, var(--warning-color, var(--orange, #ff976a)));
}
.taroify-form-feedback--invalid {
  color: var(--form-feedback-invalid-color, var(--danger-color, var(--red, #ee0a24)));
}