.taroify-switch {
  position: relative;
  box-sizing: content-box;
  display: inline-block;
  width: var(--switch-width, 2em);
  height: var(--switch-height, 1em);
  font-size: var(--switch-font-size, var(--switch-size, 60px));
  cursor: pointer;
  background: var(--switch-background-color, var(--white, #fff));
  border: var(--switch-border, var(--border-width-base, 2px) solid rgba(0, 0, 0, 0.1));
  border-radius: var(--switch-node-size, 1em);
  transition: background-color var(--switch-transition-duration, var(--animation-duration-base, 0.3s));
}
.taroify-switch__node {
  position: absolute;
  top: 0;
  left: 0;
  width: var(--switch-node-width, var(--switch-node-size, 1em));
  height: var(--switch-node-height, var(--switch-node-size, 1em));
  font-size: inherit;
  background: var(--switch-node-background-color, var(--white, #fff));
  border-radius: 100%;
  box-shadow: var(--switch-node-box-shadow, 0 6px 2px 0 rgba(0, 0, 0, 0.05), 0 4px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 6px 0 rgba(0, 0, 0, 0.05));
  transition: transform var(--switch-transition-duration, var(--animation-duration-base, 0.3s)) cubic-bezier(0.3, 1.05, 0.4, 1.05);
}
.taroify-switch__loading {
  position: absolute;
  top: 25%;
  left: 25%;
  width: 50%;
  height: 50%;
  line-height: 1;
}
.taroify-switch--checked {
  background: var(--switch-checked-background-color, var(--switch-checked-color, var(--primary-color, var(--blue, #1989fa))));
}
.taroify-switch--checked .taroify-switch__node {
  transform: translateX(var(--switch-node-translate-x, calc(var(--switch-width, 2em) - var(--switch-node-size, 1em))));
}
.taroify-switch--checked .taroify-switch__loading {
  color: var(--switch-loading-color, var(--switch-checked-color, var(--switch-checked-color, var(--primary-color, var(--blue, #1989fa)))));
}
.taroify-switch--disabled {
  cursor: not-allowed;
  opacity: var(--switch-disabled-opacity, var(--disabled-opacity, 0.5));
}
.taroify-switch--loading {
  cursor: default;
}