.taroify-image {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
}
.taroify-image--circle {
  overflow: hidden;
  border-radius: 50%;
}
.taroify-image--circle img {
  border-radius: inherit;
}
.taroify-image--loading {
  position: absolute;
  width: 0;
  height: 0;
}
.taroify-image__fallback, .taroify-image__placeholder {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: var(--image-placeholder-font-size, var(--font-size-md, 28px));
  color: var(--image-placeholder-color, var(--gray-6, #969799));
  background-color: var(--image-placeholder-background-color, var(--background-color, var(--gray-1, #f7f8fa)));
}
.taroify-image__fallback-icon, .taroify-image__placeholder-icon {
  font-size: var(--image-placeholder-icon-size, 64px);
  color: var(--image-placeholder-icon-color, var(--gray-4, #dcdee0));
}