.taroify-rate {
  display: inline-flex;
  cursor: pointer;
  user-select: none;
}
.taroify-rate__item {
  position: relative;
  pointer-events: none;
}
.taroify-rate__item:not(:last-child) {
  padding-right: var(--rate-icon-gutter, var(--padding-base, 8px));
}
.taroify-rate__icon {
  display: block;
  width: 1em;
  font-size: var(--rate-icon-size, 40px);
  color: var(--rate-icon-empty-color, var(--gray-5, #c8c9cc));
}
.taroify-rate__icon--half {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.taroify-rate__icon--full {
  color: var(--rate-icon-full-color, var(--danger-color, var(--red, #ee0a24)));
}
.taroify-rate__icon--disabled {
  color: var(--rate-icon-disabled-color, var(--gray-5, #c8c9cc));
}
.taroify-rate--disabled {
  cursor: not-allowed;
}
.taroify-rate--readonly {
  cursor: default;
}