.taroify-calendar {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--calendar-background-color, var(--white, #fff));
}
.taroify-calendar__header {
  flex-shrink: 0;
  box-shadow: var(--calendar-header-box-shadow, 0 4px 20px rgba(125, 126, 128, 0.16));
}
.taroify-calendar__month-title, .taroify-calendar__header-title, .taroify-calendar__header-subtitle {
  height: var(--calendar-header-title-height, 88px);
  font-weight: 500;
  line-height: var(--calendar-header-title-height, 88px);
  text-align: center;
}
.taroify-calendar__header-title {
  font-size: var(--calendar-header-title-font-size, var(--font-size-lg, 32px));
}
.taroify-calendar__header-subtitle {
  font-size: var(--calendar-header-subtitle-font-size, var(--font-size-md, 28px));
}
.taroify-calendar__month-title {
  font-size: var(--calendar-month-title-font-size, var(--font-size-md, 28px));
}
.taroify-calendar__weekdays {
  display: flex;
}
.taroify-calendar__weekday {
  flex: 1;
  font-size: var(--calendar-weekdays-font-size, var(--font-size-sm, 24px));
  line-height: var(--calendar-weekdays-height, 60px);
  text-align: center;
}
.taroify-calendar__body {
  flex: 1;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.taroify-calendar__days {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  user-select: none;
}
.taroify-calendar__month-watermark {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 0;
  font-size: var(--calendar-month-watermark-font-size, 320px);
  color: var(--calendar-month-watermark-color, rgba(242, 243, 245, 0.8));
  pointer-events: none;
  transform: translate(-50%, -50%);
}
.taroify-calendar__day, .taroify-calendar__active-day {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.taroify-calendar--multiple .taroify-calendar__day--end, .taroify-calendar--multiple .taroify-calendar__day--start, .taroify-calendar--multiple .taroify-calendar__day--active, .taroify-calendar--range .taroify-calendar__day--end, .taroify-calendar--range .taroify-calendar__day--start, .taroify-calendar--range .taroify-calendar__day--active {
  color: var(--calendar-range-edge-color, var(--white, #fff));
  background: var(--calendar-range-edge-background-color, var(--calendar-active-color, var(--blue, #1989fa)));
}
.taroify-calendar--multiple .taroify-calendar__day--start, .taroify-calendar--range .taroify-calendar__day--start {
  border-radius: var(--border-radius-md, 8px) 0 0 var(--border-radius-md, 8px);
}
.taroify-calendar--multiple .taroify-calendar__day--end, .taroify-calendar--range .taroify-calendar__day--end {
  border-radius: 0 var(--border-radius-md, 8px) var(--border-radius-md, 8px) 0;
}
.taroify-calendar--multiple .taroify-calendar__day--active, .taroify-calendar--range .taroify-calendar__day--active {
  border-radius: 8px;
}
.taroify-calendar--multiple .taroify-calendar__day--middle {
  color: var(--calendar-range-edge-color, var(--white, #fff));
  background: var(--calendar-range-edge-background-color, var(--calendar-active-color, var(--blue, #1989fa)));
}
.taroify-calendar--range .taroify-calendar__day--middle {
  color: var(--calendar-range-middle-color, var(--calendar-active-color, var(--blue, #1989fa)));
}
.taroify-calendar--range .taroify-calendar__day--middle::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: "";
  background-color: currentColor;
  opacity: var(--calendar-range-middle-background-opacity, 0.1);
}
.taroify-calendar__day {
  position: relative;
  width: 14.285%;
  height: var(--calendar-day-height, 128px);
  font-size: var(--calendar-day-font-size, var(--font-size-lg, 32px));
  cursor: pointer;
}
.taroify-calendar__day--placeholder {
  width: 100%;
}
.taroify-calendar__day--disabled {
  color: var(--calendar-day-disabled-color, var(--gray-5, #c8c9cc));
  cursor: default;
}
.taroify-calendar__day__top, .taroify-calendar__day__bottom {
  position: absolute;
  right: 0;
  left: 0;
  font-size: var(--calendar-day-info-font-size, var(--font-size-xs, 20px));
  line-height: var(--calendar-day-info-line-height, var(--line-height-xs, 28px));
}
@media (max-width: 350px) {
  .taroify-calendar__day__top, .taroify-calendar__day__bottom {
    font-size: 9px;
  }
}
.taroify-calendar__day__top {
  top: 6px;
}
.taroify-calendar__day__bottom {
  bottom: 6px;
}
.taroify-calendar__active-day {
  width: var(--calendar-active-day-size, 108px);
  height: var(--calendar-active-day-size, 108px);
  color: var(--calendar-active-day-color, var(--white, #fff));
  background: var(--calendar-active-day-background-color, var(--calendar-active-color, var(--blue, #1989fa)));
  border-radius: var(--calendar-active-day-border-radius, var(--border-radius-md, 8px));
}
.taroify-calendar__footer {
  flex-shrink: 0;
  padding: var(--calendar-footer-padding, 0 var(--padding-md, 32px));
}
.taroify-calendar__confirm {
  height: var(--calendar-confirm-button-height, 72px);
  margin: var(--calendar-confirm-button-margin, 14px 0);
  background: var(--calendar-confirm-button-color, var(--calendar-active-color, var(--blue, #1989fa)));
  border-color: var(--calendar-confirm-button-color, var(--calendar-active-color, var(--blue, #1989fa)));
}