.taroify-radio {
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  font-size: var(--radio-font-size, var(--font-size-lg, 32px));
  cursor: pointer;
  user-select: none;
}
.taroify-radio--disabled {
  cursor: not-allowed;
}
.taroify-radio--horizontal {
  margin-right: var(--radio-gap, var(--padding-sm, 24px));
}
.taroify-radio--horizontal:last-child {
  margin-right: 0;
}
.taroify-radio--vertical {
  margin-bottom: var(--radio-gap, var(--padding-sm, 24px));
}
.taroify-radio--vertical:last-child {
  margin-bottom: 0;
}
.taroify-radio__icon {
  flex: none;
  height: 1em;
  font-size: var(--radio-icon-font-size, var(--radio-size, 40px));
  line-height: 1em;
  cursor: pointer;
}
.taroify-radio__icon .taroify-icon {
  box-sizing: border-box;
  display: block;
  width: 1.25em;
  height: 1.25em;
  font-size: 0.8em;
  line-height: 1.25;
  color: transparent;
  text-align: center;
  border: 1px solid var(--radio-border-color, var(--gray-5, #c8c9cc));
  transition-duration: var(--radio-transition-duration, var(--animation-duration-fast, 0.2s));
  transition-property: color, border-color, background-color;
}
.taroify-radio__icon--round .taroify-icon {
  border-radius: 100%;
}
.taroify-radio__icon--checked .taroify-icon {
  color: var(--radio-checked-icon-color, var(--white, #fff));
  background: var(--radio-checked-icon-background-color, var(--primary-color, var(--blue, #1989fa)));
  border-color: var(--radio-checked-icon-border-color, var(--primary-color, var(--blue, #1989fa)));
}
.taroify-radio__icon--checked.taroify-radio__icon--disabled .taroify-icon {
  color: var(--radio-disabled-icon-color, var(--gray-5, #c8c9cc));
}
.taroify-radio__icon--disabled {
  cursor: not-allowed;
}
.taroify-radio__icon--disabled .taroify-icon {
  background: var(--radio-disabled-icon-background-color, var(--border-color, var(--gray-3, #ebedf0)));
  border-color: var(--radio-disabled-icon-border-color, var(--gray-5, #c8c9cc));
}
.taroify-radio__label {
  margin-left: var(--radio-label-margin, var(--padding-xs, 16px));
  line-height: var(--radio-label-line-height, var(--radio-size, 40px));
  color: var(--radio-label-color, var(--text-color, var(--gray-8, #323233)));
}
.taroify-radio__label--left {
  margin: 0 var(--radio-label-margin, var(--padding-xs, 16px)) 0 0;
}
.taroify-radio__label--disabled {
  color: var(--radio-disabled-label-color, var(--gray-5, #c8c9cc));
}

.taroify-radio-group--horizontal {
  display: flex;
  flex-wrap: wrap;
}
.taroify-radio-group--vertical .taroify-radio {
  display: flex;
}
.taroify-radio-group .taroify-cell__value {
  flex: 0;
}